h1
{
  text-align: center;
}
.calculator {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  border: 2px solid #000000;
  border-radius: 5px;
  background-color: #f8f8f8;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  text-align: center;


}

.calculator input[type="text"] {
  width: calc(100% - 20px);
  margin-bottom: 10px;
  padding: 10px;
  font-size: 20px;
  border: 2px solid #000000;
  border-radius: 5px;
  background-color: #d6fddff7;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
 
  border-radius: 5px;
}

.keypad button {
  width: 130px;
  height: 50px;
  margin: 5px;
  font-size: 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.keypad button:hover {
  background-color: #ddd;
}

#clear {
  background-color: #ff6347;
  color: #fff;
}

#result {
  background-color: #2ecc71;
  color: #fff;
}

.calculator button[name="("],
.calculator button[name=")"] {
  background-color: #3498db;
  color: #fff;
}

.calculator button[name="/"],
.calculator button[name="*"],
.calculator button[name="-"],
.calculator button[name="+"] {
  background-color: #f39c12;
  color: #fff;
}

.calculator button[name="."],
.calculator button[name="0"] {
  background-color: #95a5a6;
  color: #fff;
}
